* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
}

.container {
    width: 100%;
    margin: 0 auto;
}

.qr-reader {
    padding: 0 2%;
    border-radius: 10px;
}

#Qrcode {
    background-color: #1f1e1e;
    min-height: 100vh;
}

.container header {
    text-align: center;
}

header h1{
    padding: 30px 0;
    color: #b721ff;

}

.line {
    width: 100%;
    height: 5px;
    background: -webkit-linear-gradient(to left, #21d4fd, #b721ff );
    background: -o-linear-gradient(to left, #21d4fd, #b721ff );
    background: -moz-linear-gradient(to left, #21d4fd, #b721ff );
    background: linear-gradient(to left, #21d4fd, #b721ff );
}

.buttons-top{
    text-align: center;
    margin-top: 30px;
}

.btn-home{
    width: 150px;
    height: 50px;
    text-transform: uppercase;
    color: azure;
    font-weight: bolder;
    border-radius: 10px;
    cursor: pointer;
    border: none;
    margin: 0 10px;
    background: -webkit-linear-gradient(to left, #21d4fd, #b721ff );
    background: -o-linear-gradient(to left, #21d4fd, #b721ff );
    background: -moz-linear-gradient(to left, #21d4fd, #b721ff );
    background: linear-gradient(to left, #21d4fd, #b721ff );
}

.resultado {
    width: 100%;
    text-align: center;
    padding: 50px 0;
}
.result {
    margin: 0 auto;
    color: white;
}