*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.container {
    width: 100%;
    margin: 0 auto;
}

.container-login{
    width: 100%;
    min-height: 100vh;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    padding: 15px;
    background-color: #1f1e1e;
}

.wrap-login{
    width: 390px;
    background-color: #333;
    border-radius: 15px;
    padding: 77px 55px 33px 55px;
    overflow: hidden;
}

.login-form{
    width: 100%;
}

.login-form-title{
    display: block;
    font-size: 30px;
    color: azure;
    line-height: 1.2;
    text-align: center;
    margin-bottom: 30px;

}

.wrap-input{
    width: 100%;
    position: relative;
    border-bottom: 2px solid #adadaa;
    margin-bottom: 37px;
}
.input{
    font-size: 15px;
    color: #fff;
    line-height: 1.2;
    border: none;
    display: block;
    width: 100%;
    height: 45px;
    background-color: transparent;
    padding: 0 15px;
}

.input:focus{
    outline: none;
}

.focus-input{
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    pointer-events: none;
    color: #adadad;
}

.focus-input::before{
    content: "";
    display: block;
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 0;
    height: 2px;

    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    -moz-transition: all 0.4s;
    background: -webkit-linear-gradient(to left, #21d4fd, #b721ff );
    background: -o-linear-gradient(to left, #21d4fd, #b721ff );
    background: -moz-linear-gradient(to left, #21d4fd, #b721ff );
    background: linear-gradient(to left, #21d4fd, #b721ff );
}

.focus-input::after{
    font-size: 15px;
    color: #9999;
    content: attr(data-placeholder);
    display: block;
    width: 100%;
    position: absolute;
    top: 16px;
    left: 0;
    padding-left: 5px;
    -webkit-transition: all 0.4s;
    -o-transition: all 0.4s;
    -moz-transition: all 0.4s;

}

.has-val + .focus-input::after{
    top: -15px;
}
.has-val + .focus-input::before{
    width: 100%;
}

.input:focus + .focus-input::after{
    top: -15px;
}

.input:focus + .focus-input::before{
    width: 100%;
}

.container-login-form-btn{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-bottom: 13px;
}

.login-form-btn{
    font-size: 15px;
    color: white;
    border: none;
    border-radius: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 50px;
    cursor: pointer;
    line-height: 1.2;
    text-transform: uppercase;
    background: -webkit-linear-gradient(to left, #21d4fd, #b721ff );
    background: -o-linear-gradient(to left, #21d4fd, #b721ff );
    background: -moz-linear-gradient(to left, #21d4fd, #b721ff );
    background: linear-gradient(to left, #21d4fd, #b721ff );
}
.login-form-btn:hover{
    -webkit-transition:  0.7s;
    -o-transition:  0.7s;
    -moz-transition:  0.7s;
    background: -webkit-linear-gradient(to left, #6bdaf3, #ca74f1 );
    background: -o-linear-gradient(to left, #6bdaf3, #ca74f1 );
    background: -moz-linear-gradient(to left, #6bdaf3, #ca74f1 );
    background: linear-gradient(to left, #6bdaf3, #ca74f1 );
}

.text-center{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 30px;
    font-size: 18px;
}

.txt1 {
    color: white;
    padding-right: 15px;

}

.txt2 {
    text-decoration: none;
    color: #b721ff;
}